.header{
  height: 200px;
  width: 100%;
  background-color : #E9EEF6;
  padding-top: 50px;
}
/**** Card ****/
.card-wrapper{
  width: 270px; 
  height: 200px; 
  box-shadow: 0px 3px 50px #A5A5A5;
  display: flex;
  flex-direction: column; 
  margin-right: 20px;
}

.card-top{
  width: 100%;
  height: 2%;
}
.recipe-holder{
 width: 100%;
  height: 98%;
  padding: 10px 10px;
  display : flex;
  flex-direction: column;
  position: relative;
}
.card-header{
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 80px;
  height: 30px;
  padding: 1px 1px !important;
  text-align: center;
}
/*** RecipeList Card ***/
.recipe-container{
  height: 600px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: #F6F7F8;
  padding : 40px 100px;

}


.ingredients > li {
  list-style-type: disc !important;
  padding-left: 1em !important;
  margin-left: 1em;
}